.container{
    position: relative;
    height: 70vh;
    overflow: hidden;
}

.itemList{
    background-color: brown;
    overflow:auto;
    margin: 0 auto;
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
    height: 90%;
 }

.item{
    background-color: blue;
    width: 16vw;
    padding: 10px;
    margin: 4px;
    float:left;
}

.item img{
    width: 16vw;
}

.item div{
    width: 16vw;
    text-align: center;
}


.overShow{
    width: 100%;
    background: rgba(250, 153, 153, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
}

.overHidden{
    width: 100%;
    background: rgba(250, 153, 153, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    display: none;
}

.pagingDiv{
    text-align: center;
}

.paging{
    cursor: pointer;
    padding: 6px;
}

.current{
    font-size: 1.1em;
    color: #f8204b;
    text-decoration: underline;

}

