.container{
    position: relative;
 
}

.under{
    display: grid;
    grid-template-areas: 
    'subchapter items';
    grid-template-columns: 1fr 3fr;
    
}

.over{
    /* height: 400px; */
    width: 100%;
    background: rgba(105, 105, 105, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    
}

.item{
    padding: 6px;
}

.subchapters{
    grid-area: subchapter; 
}

.items{
    grid-area: items;
}



