
 .appWraper{
  display: grid;
  height: 100vh;
  grid-template-areas: 
    'header header header'
    'menu content details' 
    'footer footer footer';
    grid-template-columns: 20% 20% auto;
    grid-template-rows: 15vh 70vh 15vh;
    grid-gap: 4px;
}

.header{
  grid-area: header;
  background-color: khaki;
}

.menu{
  grid-area: menu;
}

.content{
  grid-area: content;
  background-color: lightcoral;
  overflow-y: scroll;

}

.details{
  grid-area: details;
  overflow-y: scroll;
}

.footer{
  grid-area: footer;
  background-color: khaki;
}



