.menuItem {
    padding: 0 40px;
    margin: 5px 10px;
    user-select: none;
    cursor: pointer;
    border: none;
  }

  .menuItem img{
    width: 10vw;
    pointer-events: none;
  }

  .menuItemWrapper.active {
    border: 1px blue solid;
  }
  .menuItem.active {
    border: 1px green solid;
  }
   
  .scrollMenuArrow {
    padding: 20px;
    cursor: pointer;
  }